/* inter-regular - latin_cyrillic */
@font-face {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 400;
  src: url('../../vendor/fonts/inter/inter-v8-latin_cyrillic-regular.eot'); /* IE9 Compat Modes */
  src: local(''),
       url('../../vendor/fonts/inter/inter-v8-latin_cyrillic-regular.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
       url('../../vendor/fonts/inter/inter-v8-latin_cyrillic-regular.woff2') format('woff2'), /* Super Modern Browsers */
       url('../../vendor/fonts/inter/inter-v8-latin_cyrillic-regular.woff') format('woff'), /* Modern Browsers */
       url('../../vendor/fonts/inter/inter-v8-latin_cyrillic-regular.ttf') format('truetype'), /* Safari, Android, iOS */
       url('../../vendor/fonts/inter/inter-v8-latin_cyrillic-regular.svg#Inter') format('svg'); /* Legacy iOS */
}
